@value MakeBlue,MakeBlack,MakeLightBlue,MakeGrey,MakeLightGrey,MakeYellow from '../../common/color.css';

* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
}

.card {
  width: calc(100% / 3.5);
  border: 1px solid MakeLightGrey;
  border-radius: 3px;
  padding: 10px 15px;
  margin-bottom: 15px;
  margin: 15px;
}

.card:hover {
  border: 1px solid MakeLightBlue;
}

.titleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}

.titleContainer > h2 {
  margin: 0;
}

.client {
  margin: 8px 0px;
}
.due {
  color: MakeGrey;
  margin-top: 28px;
}

.status {
  width: 50px;
  text-align: center;
  border: 1px solid MakeYellow;
  border-radius: 45px;
  padding: 4px;
  font-size: 0.8rem;
  color: MakeYellow;
}

.line {
  width: 98%;
  height: 2px;
  margin: auto;
  background-color: MakeLightGrey;
}
.bottom {
  margin-top: 30px;
  margin-bottom: 15px;
}

td {
  width: 25%;
  padding: 4px;
}

.orderBold {
  font-weight: 700;
}

.reqBtn,
.chatBtn {
  margin: 15px 4px;
  padding: 8px 10px;
  background-color: transparent;
  border: none;
  border-radius: 0.2rem;
  outline: none;
  font-size: 0.9rem;
  cursor: pointer;
}

.reqBtn {
  color: white;
  background-color: MakeLightBlue;
}

.chatBtn {
  border: 1px solid MakeLightBlue;
  color: MakeLightBlue;
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
    border: 1px solid MakeLightGrey;
    border-radius: 3px;
    margin: 10px auto;
  }
}
